.hamburger {
	padding: 15px 0px 5px 0px;
	display: inline-block;
	cursor: pointer;
	transition-property: all;
	transition-duration: 0.15s;
	transition-timing-function: linear;
	font: inherit;
	color: inherit;
	text-transform: none;
	background-color: transparent;
	border: 0;
	margin: 0;
	overflow: visible; }

.hamburger-box {
	width: 45px;
	height: 0px;
	display: inline-block;
	position: relative; }

.hamburger-inner {
	display: block;
	top: 50%;
	margin-top: -7px;
	width: 45px;
	height: 6px;
	background-color: $green;
	border-radius: 4px;
	position: absolute;
	transition: all 0.15s ease;
	&:before, &:after {
		width: 45px;
		height: 6px;
		background-color: $green;
		position: absolute;
		transition: all 0.15s ease; }
	&:before, &:after {
		content: "";
		display: block; }
	&:before {
		top: -10px; }
	&:after {
		top: -20px; } }
.hamburger--collapse .hamburger-inner {
	top: auto;
	bottom: 0; }

.hamburger--collapse.is-active .hamburger-inner {
	transform: translate3d(0, -6px, 0) rotate(-45deg);
	transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
	&:after {
		top: 0;
		opacity: 0; }
	&:before {
		top: 0;
		transform: rotate(-90deg); } }


.mobile-nav {
	display: none;
	color: white;
	font-size: 18px;
	border-top: 1px solid #b9fced;
	background: $primary;
	@include mq($from: mobile) {
		display: none !important; }
	.mobile-nav-list {
		list-style: none;
		margin: 0;
		padding: 20px 0;
		li {
			margin: 0;
			display: block; }
		a {
			display: block;
			border-bottom: 1px solid #b9fced;
			padding: 6px 15px;
			transition: all .4s ease;
			&:hover {
				background: rgba(white, .1); }
			&:active, &:focus {
				text-decoration: none;
				color: white; } } }
	.mobile-nav-sublvl {
		display: none;
		font-size: 16px;
		padding-left: 20px;
		list-style: circle;
		background: rgba(white, .1);
		&.active {
			display: block; }
		a {
			border-bottom: none; } } }
